<template>
  <div class="shoppingCart-container">
    <!-- <headerUsually btnShow="true" titleText="已选菜项" url="/mobileOrdering/orderDish"></headerUsually> -->
    <headerUsually
      btnShow="true"
      titleText="已选菜项"
      url
      :isComponents="isComponents"
      @emitGoback="gobackFun()"
    ></headerUsually>
    <div class="container-main">
      <div class="wrapper" ref="wrapper">
        <ul class="container">
          <li
            class="item"
            :class="item.pluState==1?'no-item':''"
            v-for="(item, index) in receiveItem"
            :key="index"
            @click="goSDishTastefun(item,index)"
          >
            <div class="item-content">
              <div class="num">{{index+1}}</div>
              <div class="menu-content">
                <div class="menu-name">{{item.pluDescript||item.name}}</div>
              </div>
              <div class="quantity">
                <span>X {{item.number||item.quantity}}</span>
                <span class="iconfont icon-ren"></span>
              </div>

              <div class="price">{{item.pluPrice||item.price}}</div>
              <div class="iconfont-container">
                <i
                  class="iconfont icon-jiantou1"
                  v-if="(item.posCondstDtoList&&item.posCondstDtoList.length>0)||(item.pluCondgplist&&item.pluCondgplist.length>0)"
                ></i>
              </div>
            </div>
            <div class="ingredients" v-if="item.dishNameStr">
              <span>{{item.dishNameStr.substring(0,item.dishNameStr.length-1)}}</span>
              <!-- <span>{{item.dishSPrice}}</span> -->
            </div>
          </li>
        </ul>
      </div>
    </div>
    <EditTasteMethod
      v-if="showEditTasteMethod"
      :selectedItem="selectedItem"
      @emitHiddenTasteMethod="hiddenEditTasteMethodFun($event)"
    ></EditTasteMethod>
    <EditPrivateTaste
      v-if="showEditPrivateTasteCom"
      :selectedItem="selectedItem"
      @emitHiddenPrivateTaste="hiddenEditPrivateTasteFun($event)"
    ></EditPrivateTaste>
  </div>
</template>
<script>
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import EditTasteMethod from "@/components/common/mobileOrdering/EditTasteMethod";
import EditPrivateTaste from "@/components/common/mobileOrdering/EditPrivateTaste.vue";
import BScroll from "better-scroll";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    headerUsually,
    EditTasteMethod,
    EditPrivateTaste
  },
  data() {
    return {
      isComponents: true,
      receiveItem: {},
      showEditTasteMethod: false,
      showEditPrivateTasteCom: false,
      selectedItem: {},
      selectedIndex: -1,
      tempDishIndex: -1
    };
  },
  computed: {
    ...mapState({
      shoppingCart: state => state.mobileOrdering.shoppingCart
    })
  },
  created() {
    this.receiveItem = _.cloneDeep(this.shoppingCart);
    console.log("shoppingCart:", this.shoppingCart);
    let tempDishList = [];

    this.receiveItem.forEach((element, index) => {
      this.$set(element, "index", index);
      if (element.pluFlag.indexOf("1") == 1) {
        let dishNameStr = "";
        let dishSPrice = 0;
        element.posCondstDtoList.forEach(ele => {
          ele.posConddtlList.forEach(opt => {
            if (opt.selected) {
              if (opt.price > 0) {
                dishNameStr += `${opt.descript}(${opt.price}),`;
              } else {
                dishNameStr += opt.descript + ",";
              }
              dishSPrice += opt.price * element.number;
            }
          });
        });
        this.$set(element, "dishNameStr", dishNameStr);
        this.$set(element, "dishSPrice", dishSPrice);
      } else if (element.pluFlag.indexOf("1") == 2) {
        this.tempDishIndex = index;
        tempDishList = element.tempDishList;
      } else {
        if (element.pluCondgplist.length > 0) {
          let dishNameStr = "";
          let dishSPrice = 0;
          element.pluCondgplist.forEach(ele => {
            if (ele.selected) {
              if (ele.price > 0) {
                dishNameStr += `${ele.descript}(${ele.price}),`;
              } else {
                dishNameStr += ele.descript + ",";
              }
              dishSPrice += ele.price * element.number;
            }
          });
          this.$set(element, "dishNameStr", dishNameStr);
          this.$set(element, "dishSPrice", dishSPrice);
        }
      }
    });
    if (this.tempDishIndex > -1) {
      this.receiveItem.splice(this.tempDishIndex, 1);
    }
    this.receiveItem.push(...tempDishList);

    //console.log("this.receiveItem:", this.receiveItem);
    ///console.log("shoppingCart2:", JSON.stringify(this.shoppingCart));
  },
  mounted() {
    this.$nextTick(() => {
      let scrollLeft = new BScroll(this.$refs.wrapper, { click: true });
    });
  },

  methods: {
    gobackFun() {
      // console.log("this.tempDishIndex:", this.tempDishIndex);
      //console.log("this.shoppingCart3:", JSON.stringify(this.shoppingCart));
      if (this.tempDishIndex > -1) {
        let tempDish = this.shoppingCart[this.tempDishIndex];
        //console.log("tempDish:", tempDish);
        this.receiveItem.splice(this.tempDishIndex, 0, tempDish);
        // console.log("this.receiveItem:", this.receiveItem);
        this.receiveItem = this.receiveItem.slice(0, this.shoppingCart.length);
      }
      //console.log("this.receiveItem:", this.receiveItem);
      this.$store.commit("sumbitShoppingCart", this.receiveItem);
      this.$router.push("/mobileOrdering/orderDish");
    },
    goSDishTastefun(item, index) {
      console.log("item:", item);
      this.selectedItem = item;
      this.selectedIndex = index;
      if (!item.pluFlag) {
        return;
      }
      // if (item.pluFlag.indexOf("1") == 0) {
      //   return;
      // }
      if (item.posCondstDtoList && item.posCondstDtoList.length > 0) {
        this.showEditTasteMethod = true;
      } else if (item.pluCondgplist && item.pluCondgplist.length > 0) {
        this.showEditPrivateTasteCom = true;
      }
    },
    hiddenEditPrivateTasteFun(item) {
      if (item) {
        //console.log("item:shoppingCart:", item);
        this.receiveItem.splice(this.selectedIndex, 1, item);
        let dishNameStr = "";
        let dishSPrice = 0;
        this.receiveItem[this.selectedIndex].pluCondgplist.forEach(ele => {
          if (ele.selected) {
            if (ele.price > 0) {
              dishNameStr += `${ele.descript}(${ele.price}),`;
            } else {
              dishNameStr += ele.descript + ",";
            }
            dishSPrice +=
              ele.price * this.receiveItem[this.selectedIndex].number;
          }
        });
        this.receiveItem[this.selectedIndex].dishNameStr = dishNameStr;
        this.receiveItem[this.selectedIndex].dishSPrice = dishSPrice;
      }
      this.showEditPrivateTasteCom = false;
    },
    hiddenEditTasteMethodFun(item) {
      if (item) {
        //console.log(item);
        this.receiveItem.splice(this.selectedIndex, 1, item);
        let dishNameStr = "";
        let dishSPrice = 0;
        this.receiveItem[this.selectedIndex].posCondstDtoList.forEach(ele => {
          ele.posConddtlList.forEach(opt => {
            if (opt.selected) {
              if (opt.price > 0) {
                dishNameStr += `${opt.descript}(${opt.price}),`;
              } else {
                dishNameStr += opt.descript + ",";
              }
              dishSPrice +=
                opt.price * this.receiveItem[this.selectedIndex].number;
            }
          });
        });
        this.receiveItem[this.selectedIndex].dishNameStr = dishNameStr;
        this.receiveItem[this.selectedIndex].dishSPrice = dishSPrice;
      }
      this.showEditTasteMethod = false;
    }
  }
};
</script>
<style lang="less" scoped>
.shoppingCart-container {
  .container-main {
    .wrapper {
      position: absolute;
      top: 46px;
      bottom: 0px;
      width: 100%;
      background-color: #fff;
      z-index: 0;
      .item {
        margin: 0 18px;
        padding: 10px 0;
        text-align: left;
        border-bottom: 1px dashed #ddd;
        color: #000000;
        .item-time {
          font-size: 12px;
          .date {
            margin: 0 5px;
          }
        }
        .item-content {
          margin-top: 3px;

          display: flex;
          align-items: center;
          .num {
            width: 25px;
          }
          .menu-content {
            flex: 1;
            text-align: left;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            .menu-name {
              max-width: 200px;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
              font-size: 14px;
            }
          }
          .quantity {
            width: 50px;
            margin-right: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .icon-ren {
              text-align: right;
              color: #c4ad68;
            }
          }
          .price {
            width: 50px;
          }
          .iconfont-container {
            width: 16px;
            .icon-jiantou1 {
              color: #a8acb8;
            }
          }
        }
        .ingredients {
          font-size: 12px;
          color: #dd0a09;

          margin-left: 13px;
          word-wrap: break-word;
          display: flex;
          justify-content: space-between;
          // overflow: hidden;
          // white-space: nowrap;
          // text-overflow: ellipsis;
          span {
            transform: scale(0.85);
          }
          span:first-child {
            width: 170px;
          }
          span:last-child {
            margin-right: 50px;
          }
        }
      }
      .no-item {
        color: #dd0a09;
      }
      .no-list {
        margin-top: 150px;
      }
    }
  }
}
</style>



