<template>
  <div class="tasteMethod">
    <!-- <headerUsually btnShow="true" titleText="口味烧法" url="/mobileOrdering/SDishOperation"></headerUsually> -->
    <headerUsually
      btnShow="true"
      titleText="口味烧法"
      url
      :isComponents="isComponents"
      @emitGoback="gobackFun()"
    ></headerUsually>
    <div class="container-wrapper">
      <div class="left" ref="wrapperLeft">
        <ul class="container">
          <li
            class="left-item"
            :class="leftIndex==index?'left-item-active':''"
            v-for="(item, index) in receiveItem.posCondstDtoList"
            :key="index"
            @click="leftClickFun(item,index)"
          >{{item.descript}}</li>
          <!-- <li class="left-item">------- ------</li>
          <li class="left-item left-item-active">------- --</li>-->
        </ul>
      </div>
      <div class="right" ref="wrapperRight">
        <ul class="container">
          <li
            class="right-item"
            :class="item.selected?'right-item-active':''"
            @click="rightClickFun(item,index)"
            v-for="(item, index) in rightArr"
            :key="index"
          >
            <span class="text" v-if="item.price>0">{{item.descript}} (+{{item.price}})</span>
            <span class="text" v-else>{{item.descript}}</span>
            <span class="iconfont icon-xuanzhong" v-if="item.selected"></span>
          </li>
          <!-- <li class="right-item">
            <span class="text">11111</span>
            <span class="iconfont icon-xuanzhong"></span>
          </li>
          <li class="right-item right-item-active">
            <span class="text">11111</span>
            <span class="iconfont icon-xuanzhong"></span>
          </li>-->
        </ul>
      </div>
    </div>
    <div class="container-remarks">
      <textarea class="remarks" placeholder="备注:" v-model="receiveItem.remarks" />
    </div>
    <div class="btn" @click="okFun()">确定</div>
  </div>
</template>
<script>
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import BScroll from "better-scroll";
export default {
  components: {
    headerUsually
  },
  props: {
    selectedItem: Object
  },
  data() {
    return {
      rightArr: [],
      isComponents: true,
      scrollRight: {},
      leftIndex: 0
    };
  },
  created() {
    console.log("tasteMethod:", this.selectedItem);
    this.receiveItem = _.cloneDeep(this.selectedItem);
    this.receiveItem.posCondstDtoList.forEach(ele => {
      ele.posConddtlList.forEach(opt => {
        if (!opt.selected) {
          this.$set(opt, "selected", false);
        }
      });
    });
    this.rightArr = this.receiveItem.posCondstDtoList[0].posConddtlList;
  },
  mounted() {
    this.$nextTick(() => {
      let scrollLeft = new BScroll(this.$refs.wrapperLeft, { click: true });
      this.scrollRight = new BScroll(this.$refs.wrapperRight, { click: true });
    });
  },
  methods: {
    gobackFun() {
      this.$emit("emitHiddenTasteMethod");
    },
    leftClickFun(item, index) {
      if (this.leftIndex == index) {
        return;
      }
      this.leftIndex = index;
      this.rightArr = this.receiveItem.posCondstDtoList[index].posConddtlList;
      this.$nextTick(() => {
        this.scrollRight.refresh();
        this.scrollRight.scrollTo(0, 0);
      });
    },
    rightClickFun(item, index) {
      item.selected = !item.selected;
    },
    okFun() {
      //console.log("this.receiveItem:", this.receiveItem);
      this.$emit("emitHiddenTasteMethod", this.receiveItem);
    }
  }
};
</script>
<style lang="less" scoped>
.tasteMethod {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 9;
  top: 0px;
  background-color: #f6f8f9;
  .container-wrapper {
    background-color: #f6f8f9;
    position: absolute;
    width: 100%;
    top: 46px;
    bottom: 160px;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    .left {
      width: 140px;
      .left-item {
        height: 55px;
        line-height: 55px;
        text-align: left;
        font-size: 14px;
        color: #333333;
        padding-left: 13px;
        padding-right: 13px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      .left-item-active {
        background-color: #ffffff;
      }
    }
    .right {
      flex: 1;
      .right-item {
        padding-left: 18px;
        padding-right: 15px;
        height: 55px;
        line-height: 55px;
        font-size: 14px;
        color: #666666;
        border-bottom: 1px solid #ddd;
        display: flex;
        align-content: center;
        justify-content: space-between;
        background-color: #fff;
        font-size: 14px;
        .iconfont {
          display: none;
        }
      }
      .right-item-active {
        color: #c4ad68;
        .iconfont {
          display: block;
          color: #c4ad68;
        }
      }
    }
  }
  .container-remarks {
    position: fixed;
    bottom: 60px;
    width: 100%;
    .remarks {
      width: 100%;
      height: 80px;
      outline: none;
      border: none;
      padding: 10px;
      box-sizing: border-box;
    }
  }
  .btn {
    position: fixed;
    bottom: 0;
    width: 100%;
    margin-top: 20px;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 11px;
  }
}
</style>



